import React from 'react';
import './App.css';
import BarChart from './Components/BarChart';
import AnyIllnessSample from './Helpers/sample-any-illness';
import SpecificIllnessSample from './Helpers/sample-specific-illness';
import 'typeface-roboto';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

function App() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className="App">
    <Tabs
      value={value}
      indicatorColor="primary"
      textColor="primary"
      onChange={handleChange}
      aria-label="vis tabs"
    >
      <Tab label="Visualization 1" value={value} index={0}/>
      <Tab label="Visualization 2" value={value} index={1} />
    </Tabs>
      <BarChart title="12 month prevalence of any mental illness (all U.S. adults)"
                  group="Adult groups"
                  sample={AnyIllnessSample} scale={40} id="any-illness"></BarChart>
    <BarChart title="12 month prevalence of common mental illnesses (all U.S. adults)"
                group="Illnesses"
                sample={SpecificIllnessSample} scale={20} id="specific-illness"></BarChart>
    </div>
  );
}

export default App;
